  .footer__item--services {
    order: 1;
  }
  .footer__item--membership {
    order: 3;
  }
  .footer__item--promo {
    order: 0;
  }
  .footer__item--trainers {
    order: 2;
  }
  .footer__item--schedule {
    order: 4;
  }
}
.social-list {
  max-width: 97px;
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  gap: 11px;
}

.social-list__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 25px;
  height: 25px;
  background: #ffffff;
  border-radius: 50%;
  cursor: pointer;
}
.social-list__link:hover, .social-list__link:focus, .social-list__link:focus-visible {
  background: #103aae;
  outline: none;
}
.social-list__link:active {
  background: #102654;
}
.social-list__link:disabled {
  background: #ffffff;
  opacity: 30%;
}
.social-list__link svg, .social-list__link:disabled > svg {
  fill: #030a1a;
}
.social-list__link:hover > svg, .social-list__link:focus > svg, .social-list__link:active > svg {
  fill: #ffffff;
}

@media (max-width: 767px) {
  .social-list {
    max-width: 100%;
  }
}